//  header 
.header {
  padding: 1em 0;
  justify-content: space-between;
  position: fixed;
  z-index: 3;
  width: 100%;
  background: rgba(#000,.5);
  &__content {
     display: flex;
     justify-content: space-between;
  }
  &__logo {
     max-width: 140px;
     align-self: center;
  }
}